import React, { useState } from "react";
import './block.css';
import './mar.css';
import './textbox.css';
import './buttons.css'

function App() {
  let [text, setText] = useState("");
  function Text1() {
    setText(text = "Вы нажали на кнопку 1");
  }
  function Text2() {
    setText(text = "Вы нажали на кнопку 2");
  }
  function Text3() {
    setText(text = "Вы нажали на кнопку 3");
  }
  return (
    <div className="block">
      <div className="buttons">
        <button onClick={Text1}><b>Кнопка_1</b></button>
        <button className='mar' onClick={Text2}><b>Кнопка_2</b></button>
        <button onClick={Text3}><b>Кнопка_3</b></button>
      </div>
      <div className="textbox"><b>{text}</b></div>
    </div>
  );
}

export default App;
